import React from "react";
import classNames from "classnames";

import "./index.scss";

const baseClass = "mail-form";
const classes = classNames(baseClass);

const MailForm = () => {
  return (
    <form className={`${classes}`} id="contact-form">
      <div className={`${classes}__input-group`}>
        <input
          className={`${classes}__input`}
          type="text"
          name="user_name"
          placeholder="Name"
        />
        <label htmlFor="user_name" className={`${classes}__label`}>
          Name
        </label>
      </div>
      <div className={`${classes}__input-group`}>
        <input
          className={`${classes}__input`}
          type="email"
          name="user_email"
          placeholder="Email"
        />
        <label htmlFor="user_email" className={`${classes}__label`}>
          Email
        </label>
      </div>

      <div className={`${classes}__input-group`}>
        <input
          className={`${classes}__input`}
          type="tel"
          name="user_tel"
          placeholder="Telefon Nummer"
        />
        <label htmlFor="user_tel" className={`${classes}__label`}>
          Telefon Nummer
        </label>
      </div>
      <div className={`${classes}__input-group`}>
        <input
          className={`${classes}__input`}
          type="text"
          name="user_desired-agent"
          placeholder="Ansprechpartner"
        />
        <label htmlFor="user_desired-agent" className={`${classes}__label`}>
          Ansprechpartner
        </label>
      </div>
      <div className={`${classes}__input-group`}>
        <textarea
          className={`${classes}__input is-textarea`}
          name="message"
          placeholder="Message"
        />
        <label htmlFor="message" className={`${classes}__label`}>
          Nachricht
        </label>
      </div>
      <button className={`${classes}__submit`}>Senden</button>
    </form>
  );
};

export default MailForm;
