import React from "react";
import "./index.scss";
import classNames from "classnames";

const baseClass = "picture";

const Picture = ({
  imageSrcSmall,
  imageSrcMedium,
  imageSrcLarge,
  imageTypeSmall,
  imageTypeMedium,
  imageTypeLarge,
  imageSrcFallback,
  imageAlt,
  additionalClasses,
  onClick,
}) => {
  const className = classNames(baseClass, additionalClasses);

  return (
    <picture className={className} onClick={onClick}>
      <source
        srcSet={imageSrcSmall}
        media={"(max-width: 560px"}
        type={imageTypeSmall}
      />
      <source
        srcSet={imageSrcMedium}
        media={"(max-width: 1024px"}
        type={imageTypeMedium}
      />
      <source
        srcSet={imageSrcLarge}
        media={"(min-width: 1024px"}
        type={imageTypeLarge}
      />
      <img src={imageSrcFallback} alt={imageAlt} />
    </picture>
  );
};

export default Picture;
