import React from "react";
import classNames from "classnames";

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

import "./index.scss";

const baseClass = "text-teaser";
const classes = classNames(baseClass);

const TextTeaser = ({ headline, text, alignment, backgroundColor }) => {
  return (
    <div
      className={`${classes} ${alignment == "left" ? "is-left" : "is-right"} ${backgroundColor == "white" ? "bg-white" : "bg-black"}`}
    >
      <Container>
        <Row>
          <Col xs={alignment === "left" ? 9 : { span: 9, offset: 3 }}>
            <h3 className={`${classes}__headline`}>{headline}</h3>
            <p className={`${classes}__text`}>{text}</p>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default TextTeaser;
