import React, { useEffect, useState } from "react";
import classNames from "classnames";

import Papa from "papaparse";

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

import "./index.scss";

const baseClass = "calendar-section";
const classes = classNames(baseClass);

const CalendarSection = () => {
  const [data, setData] = useState([]);
  
  useEffect(() => {
    Papa.parse("https://docs.google.com/spreadsheets/d/e/2PACX-1vTVHQJuzosuo8SPXMhC_wTUHeZzrZXDCTMUtGrYWlJCuYNJUWkTE3km2lVQkncuGf8bdJ7suokrxeeN/pub?gid=0&single=true&output=csv", {
      download: true,
      header: true,
      complete: (results) => {
        setData(results.data);
      },
    });
  }, [])
  
  const spreadsheetData = Array.from(data);

  return (
    <div className={`${classes}`}>
      <Container>
        <Row className="justify-content-md-center">
          <Col xs={12}>
            <h3 className={`${classes}__headline`}>Unsere nächsten Termine</h3>
          </Col>
        </Row>
        <Row className="justify-content-md-center">
          <Col xs={8}>
            <p className={`${classes}__text`}>
            Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Donec odio. Quisque volutpat mattis eros. Nullam malesuada erat ut turpis. Suspendisse urna nibh, viverra non, semper suscipit, posuere a, pede.{" "}
            </p>
          </Col>
        </Row>
        <Row className="justify-content-md-center">
          <Col xs={12}>
            <div className={`${classes}__inner-wrapper`}>
              <table>
                <thead>
                  <tr>
                    <th>Wann</th>
                    <th>Was</th>
                    <th>Wo</th>
                  </tr>
                </thead>
                <tbody>
                {spreadsheetData.map((item, i) => (
                  <tr key={i}>
                    <td>{item.Wann}</td>
                    <td>{item.Was}</td>
                    <td>{item.Wo}</td>
                  </tr>
                ))}
                </tbody>
              </table>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default CalendarSection;
