import React, { useState } from "react";
import classNames from "classnames";

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

import { useWindowSize } from "../../custom-hooks/hooks";

import "./index.scss";

const baseClass = "header";
const classes = classNames(baseClass);

const nav = (
  <nav className={`${classes}__nav`}>
    <ul className={`${classes}__nav-list`}>
      <li className={`${classes}__nav-list-item`}>
        <a href={"#werte"} className={`${classes}__nav-link`}>
          Unsere Werte
        </a>
      </li>
      <li className={`${classes}__nav-list-item`}>
        <a href={"#waswirtun"} className={`${classes}__nav-link`}>
          Was wir tun
        </a>
      </li>
      <li className={`${classes}__nav-list-item`}>
        <a href={"#team"} className={`${classes}__nav-link`}>
          Unser Team
        </a>
      </li>
      <li className={`${classes}__nav-list-item`}>
        <a href={"#kontakt"} className={`${classes}__nav-link`}>
          Kontakt
        </a>
      </li>
    </ul>
  </nav>
);

const Header = () => {
  const [menuOpen, setMenuOpen] = useState(false);
  const windowSize = useWindowSize();

  const toggleMenu = () => {
    setMenuOpen((prevMenuOpen) => !prevMenuOpen);
  };

  return (
    <header className={`${classes}`}>
      <Container>
        <Row className="justify-content-between">
          <Col>
            {windowSize.innerWidth <= 786 ? (
              <>
                <button
                  className={`${classes}__menu-toggle ${
                    menuOpen ? "is-open" : ""
                  }`}
                  id="menu-toggle"
                  aria-expanded="false"
                  onClick={() => toggleMenu()}
                >
                  <span class="screen-reader-text">Menu</span>
                  <svg
                    className={`${classes}__menu-toggle-icon`}
                    aria-hidden="true"
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                    x="0px"
                    y="0px"
                    viewBox="0 0 100 100"
                  >
                    <g className={`${classes}__menu-toggle-svg`}>
                      <path class="line line-1" d="M5 13h90v14H5z" />
                      <path class="line line-2" d="M5 43h90v14H5z" />
                      <path class="line line-3" d="M5 73h90v14H5z" />
                    </g>
                  </svg>
                </button>
                <div
                  className={`${classes}__offset-nav ${
                    menuOpen ? "is-open" : ""
                  }`}
                >
                  {nav}
                </div>
              </>
            ) : (
              nav
            )}
          </Col>
        </Row>
      </Container>
    </header>
  );
};

export default Header;
